@charset "UTF-8";
@font-face {
  src: url("../resources/fonts/Ubuntu-Regular.woff2") format("woff2"), url("../resources/fonts/Ubuntu-Regular.woff") format("woff");
  font-family: "Ubuntu";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  src: url("../resources/fonts/Ubuntu-Bold.woff2") format("woff2"), url("../resources/fonts/Ubuntu-Bold.woff") format("woff");
  font-family: "Ubuntu";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  src: url("../resources/fonts/Lato-Bold.woff2") format("woff2"), url("../resources/fonts/Lato-Bold.woff") format("woff");
  font-family: "Lato";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  src: url("../resources/fonts/Lato-Black.woff2") format("woff2"), url("../resources/fonts/Lato-Black.woff") format("woff");
  font-family: "Lato";
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}
html {
  line-height: 1.15;
  text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent;
  scroll-behavior: smooth;
}

body {
  margin: 0;
}

main {
  display: block;
}

h1 {
  margin: 0.67em 0;
  font-size: 2em;
}

hr {
  box-sizing: content-box;
  overflow: visible;
  height: 0;
}

pre {
  font-family: monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline dotted;
}

b,
strong {
  /* stylelint-disable-next-line font-weight-notation */
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub,
sup {
  position: relative;
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

/** * Correct the padding in Firefox. */
fieldset {
  border: none;
  padding: 0;
}

legend {
  display: block;
  box-sizing: border-box;
  width: 100%;
  max-width: 100%;
  padding: 0;
  font-size: 1.5rem;
  line-height: inherit;
  white-space: normal;
  color: inherit;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-decoration {
  appearance: none;
}

::-webkit-file-upload-button {
  appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

*,
::after,
::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

button {
  border: none;
  cursor: pointer;
}

input[type=submit],
input[type=reset],
input[type=button] {
  cursor: pointer;
}

address {
  font-style: normal;
}

a {
  display: inline-block;
  text-decoration: none;
  color: inherit;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
  max-height: 100%;
}

svg {
  vertical-align: middle;
  overflow: hidden;
}

ul,
ol {
  list-style: none;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

summary {
  display: block;
}

summary::-webkit-details-marker {
  display: none;
}

html {
  height: 100%;
}

body {
  height: 100%;
  font-family: "Ubuntu", sans-serif;
}

.page__wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.main {
  flex-grow: 1;
}

.container {
  max-width: var(--container-max-width);
  margin: 0 auto;
  padding: 0 15px;
}

.visuallyHidden {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 0;
  height: 0;
  opacity: 0;
}

:root {
  --color-white: #fff;
  --color-primary: #ff5a30;
  --color-primary-in-state: #fc3300;
  --color_grey_900: #1e212c;
  --color-grey-800: #424551;
  --color-grey-700: #787a80;
  --color-grey-600: #9a9ca5;
  --color-grey-500: #b3b7bc;
  --color-grey-400: #d7dadd;
  --color-grey-300: #e5e8ed;
  --color-grey-200: #f4f5f7;
  --color-grey-100: #f9f9f9;
  --color-mercury-grey: #e8e8ec;
  --section-offset: 120px;
  --container-nominal-width: 1230px;
  --container-max-width: calc(var(--container-nominal-width) + 15px * 2);
  --main-shadow:
    0 80px 80px -20px rgb(154 156 165 / 8%),
    0 30px 24px -10px rgb(154 156 165 / 5%),
    0 12px 10px -6px rgb(154 156 165 / 4%),
    0 4px 4px -4px rgb(30 33 44 / 3%);
}

.logo {
  --color: var(--color_grey_900);
  width: 132px;
  height: 22px;
  transition: transform 0.2s ease, outline 0.05s linear;
}
.logo__icon {
  width: 100%;
  height: 100%;
  fill: var(--color);
  object-fit: cover;
}
.logo_light {
  --color: var(--color-white);
  width: 142px;
  height: 24px;
}
.logo_light:focus {
  outline: none;
}
.logo_light:focus-visible {
  outline: 2px solid rgba(255, 255, 255, 0.85);
  outline-offset: 2px;
  border-radius: 1px;
}
.logo:focus {
  outline: none;
}
.logo:focus-visible {
  outline: 2px solid var(--color_grey_900);
  outline-offset: 2px;
  border-radius: 1px;
}
.logo:hover {
  transform: scale(1.1);
}

.menu__list {
  display: flex;
  column-gap: 40px;
  align-items: center;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.6;
}
.menu__item {
  transition: transform 0.1s ease;
  cursor: pointer;
}
.menu__item:hover {
  transform: scale(1.1);
}

.headerActions {
  display: flex;
  column-gap: 36px;
}
.headerActions__item {
  display: grid;
  grid-template-columns: repeat(2, auto);
  grid-template-rows: repeat(2, auto);
  column-gap: 12px;
  align-items: center;
}
.headerActions__item:hover {
  font-weight: inherit;
  transform: scale(1.05);
  transition: transform 0.2s ease;
}
.headerActions__icon {
  grid-column: 1/2;
  grid-row: 1/3;
  width: 40px;
  height: 40px;
  object-fit: cover;
}
.headerActions__text {
  font-weight: 400;
  font-size: 18px;
  line-height: 1.5;
  color: var(--color_grey_900);
}
.headerActions__text_title {
  font-weight: 700;
  font-size: 14px;
  color: var(--color-grey-800);
}

.pageHeading {
  --color: var(--color_grey_900);
  font-weight: 700;
  font-size: 72px;
  line-height: 1.3;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: var(--color);
}
.pageHeading_home {
  --color: var(--color-white);
}
.pageHeading_marked {
  --color: var(--color-primary);
}

.heading {
  --size: 46px;
  --line-height: 150%;
  --color: var(--color_grey_900);
  font-weight: 700;
  font-size: var(--size);
  line-height: var(--line-height);
  color: var(--color);
}
.heading_center {
  text-align: center;
}
.heading_fz32 {
  --size: 32px;
}
.heading_fz28 {
  --size: 28px;
}
.heading_fz24 {
  --size: 24px;
}
.heading_fz20 {
  --size: 20px;
}
.heading_fz18 {
  --size: 18px;
}
.heading_fz16 {
  --size: 16px;
}
.heading_tall {
  --line-height: 160%;
}
.heading_lowHeight {
  --line-height: 130%;
}
.heading_light {
  --color: var(--color-white);
}
.heading_light_transparent {
  color: rgba(255, 255, 255, 0.6);
}
.heading_grey600 {
  color: var(--color-grey-600);
}

.mainDescr {
  font-weight: 400;
  font-size: 20px;
  line-height: 1.5;
  color: var(--color-grey-700);
}
.mainDescr_home {
  color: var(--color-white);
}

.textDescr {
  --size: 16px;
  --line-height: 150%;
  --color: var(--color-grey-700);
  font-weight: 400;
  font-size: var(--size);
  line-height: var(--line-height);
  color: var(--color);
}
.textDescr_fz18 {
  --size: 18px;
}
.textDescr_fz14 {
  --size: 14px;
}
.textDescr_fz12 {
  --size: 12px;
}
.textDescr_tall {
  --line-height: 160%;
}
.textDescr_center {
  text-align: center;
}
.textDescr_light {
  --color: var(--color-white);
}
.textDescr_light_transparent {
  color: rgba(255, 255, 255, 0.6);
}
.textDescr_grey_600 {
  --color: var(--color-grey-600);
}
.textDescr_grey_900 {
  --color: var(--color_grey_900);
}
.textDescr_grey_800 {
  --color: var(--color-grey-800);
}

.btn {
  --btn-outline-active-primary: var(--color-primary);
  --btn-outline-hover-bg-primary: var(--color-primary);
  --btn-outline-hover-text-primary: var(--color-white);
  --btn-solid-primary-bg-hover: var(--color-primary-in-state);
  border-radius: 4px;
  font-weight: 700;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  color: var(--color-white);
  transition: all 0.2s ease-in-out;
}
.btn_large {
  padding: 0 40px;
  font-size: 16px;
  line-height: 3.25;
}
.btn_regular {
  padding: 0 32px;
  font-size: 14px;
  line-height: 3.1428;
}
.btn_small {
  padding: 0 24px;
  font-size: 12px;
  line-height: 3;
}
.btn_outline {
  border-style: solid;
  border-width: 1px;
  background-color: transparent;
  transition: color 0.2s ease-in-out, outline 0.07s ease-in-out, border 0.07s ease-in-out, background-color 0.2s ease-in-out;
}
.btn_outline_primary {
  border-color: var(--color-primary);
  color: var(--color-primary);
  background-color: var(--color-white);
}
.btn_outline_primary:focus {
  outline: none;
}
.btn_outline_primary:focus-visible {
  outline: 2px solid var(--color_grey_900);
  outline-offset: -3px;
  border-color: var(--color-grey-200);
}
.btn_outline_primary:hover {
  color: var(--btn-outline-hover-text-primary);
  background-color: var(--btn-outline-hover-bg-primary);
}
.btn_outline_primary:active {
  background-color: var(--btn-outline-active-primary);
}
.btn_outline_white {
  border-color: var(--color-white);
  color: var(--color-white);
}
.btn_outline_white:hover {
  border-color: var(--btn-outline-hover-bg-primary);
  color: var(--btn-outline-hover-bg-primary);
}
.btn_outline_white:active {
  border-color: var(--btn-outline-active-primary);
  color: var(--btn-outline-active-primary);
}
.btn_solid {
  border: none;
  transition: background-color 0.2s ease-in-out, outline 0.07s ease;
}
.btn_solid_primary {
  color: var(--color-white);
  background-color: var(--color-primary);
}
.btn_solid_primary:focus {
  outline: none;
}
.btn_solid_primary:focus-visible {
  outline: 2px solid var(--color_grey_900);
  outline-offset: -3px;
  border-color: var(--color-grey-200);
}
.btn_solid_primary:hover {
  background-color: var(--btn-solid-primary-bg-hover);
}
.btn_solid_primary:active {
  background-color: var(--btn-solid-primary-bg-hover);
}

.videoBlock {
  position: relative;
}
.videoBlock::before {
  content: "";
  position: absolute;
  z-index: 5;
  background-color: rgba(30, 33, 44, 0.6);
  inset: 0;
}
.videoBlock__content {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.videoBlock__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  transition: box-shadow 0.2s ease-in-out;
}
.videoBlock__btn_play {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 10;
  width: 80px;
  height: 80px;
  background-color: var(--color-primary);
  transform: translate(-50%, -50%);
}
.videoBlock__btn_play:hover {
  box-shadow: 0 0 0 16px rgba(255, 90, 48, 0.12);
}
.videoBlock__btn_mute {
  position: absolute;
  bottom: 32px;
  left: 32px;
  z-index: 10;
  width: 40px;
  height: 40px;
  background-color: rgba(255, 255, 255, 0.12);
}
.videoBlock__btn_mute:hover {
  box-shadow: 0 0 0 7px rgba(255, 255, 255, 0.12);
}
.videoBlock__btnIcon {
  fill: var(--color-white);
}
.videoBlock__btnIcon_play {
  width: 26px;
  height: 26px;
}
.videoBlock__btnIcon_mute {
  width: 20px;
  height: 20px;
}

.activityList {
  --grid-col-gap: 187.5px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(285px, auto));
  column-gap: var(--grid-col-gap);
  row-gap: 50px;
  justify-content: center;
  justify-items: center;
}
.activityList__item {
  position: relative;
}
.activityList__item:not(:nth-child(3n))::after {
  content: "";
  position: absolute;
  top: 0;
  right: calc(var(--grid-col-gap) / 2 * -1);
  width: 1px;
  height: 100%;
  background-image: url("../img/divider.svg");
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
}
@media (max-width: 1099px) {
  .activityList__item:not(:nth-child(3n))::after {
    display: none;
  }
}
.activityList__link {
  width: 285px;
  padding-top: 72px;
  background-position: top center;
  background-size: 48px 48px;
  background-repeat: no-repeat;
  transition: transform 0.2s ease-in-out;
}
.activityList__link_quality {
  background-image: url("../img/ic-like.svg");
}
.activityList__link_safety {
  background-image: url("../img/ic-hand.svg");
}
.activityList__link_comfort {
  background-image: url("../img/ic-slippers.svg");
}
.activityList__link_constract {
  background-image: url("../img/ic-contract.svg");
}
.activityList__link_helmet {
  background-image: url("../img/ic-helmet.svg");
}
.activityList__link_camera {
  background-image: url("../img/ic-camera.svg");
}
.activityList__link:hover {
  transform: scale(1.15);
}
.activityList__cardHeading {
  margin-bottom: 9px;
}

.formField {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  cursor: pointer;
}

.formField__caption {
  margin-bottom: 5px;
}

.formField__input {
  width: 100%;
}

.input {
  border: 1px solid var(--color-grey-400);
  border-radius: 4px;
  padding: 11px 16px 12px;
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
  color: var(--color-grey-800);
  background-color: var(--color-grey-200);
  transition: all 0.17s ease-in-out;
  resize: none;
}
.input::placeholder {
  font-weight: 400;
  font-size: inherit;
  line-height: inherit;
  color: var(--color-grey-600);
}
.input_large {
  padding: 13px 16px;
  font-size: 16px;
  line-height: 160%;
}
.input_small {
  padding: 9px 12px;
  font-size: 12px;
}
.input_light {
  border: 1px solid rgba(255, 255, 255, 0.2);
  background: rgba(255, 255, 255, 0.12);
}
.input_light::placeholder {
  font-weight: 400;
  font-size: inherit;
  line-height: inherit;
  color: rgba(255, 255, 255, 0.6);
}
.input_light:focus {
  outline: none;
  box-shadow: none;
  color: var(--color-white);
  background-color: rgba(255, 255, 255, 0.45);
}
.input:focus {
  outline: 1px solid rgba(255, 90, 48, 0.4);
  box-shadow: 0 80px 80px -20px rgba(255, 90, 48, 0.08), 0 30px 24px -10px rgba(255, 90, 48, 0.05), 0 12px 10px -6px rgba(255, 90, 48, 0.04), 0 4px 4px -4px rgba(30, 33, 44, 0.03);
  background-color: var(--color-white);
}

.servicesList {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(230px, auto));
  gap: 30px;
}
.servicesList__item {
  border-radius: 4px;
}
.servicesList__card {
  position: relative;
  display: flex;
  flex-direction: column;
  row-gap: 24px;
  align-items: center;
  border: 1px solid #e5e8ed;
  border-radius: 4px;
  padding: 89px 15px 90px;
  box-shadow: var(--main-shadow);
  background-color: var(--color-white);
  transition: all 0.3s linear;
}

.servicesCard {
  overflow: hidden;
}
.servicesCard__bg {
  position: absolute;
  display: block;
  opacity: 0;
  transition: all 0.17s ease-in-out;
  inset: 0;
}
.servicesCard__bg::before {
  content: "";
  position: absolute;
  background-color: rgba(30, 33, 44, 0.6);
  inset: 0;
}
.servicesCard__bg img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.servicesCard__icon {
  position: relative;
  z-index: 10;
  width: 52px;
  height: 52px;
  fill: var(--color-primary);
  transition: all 0.17s ease-in-out;
}
.servicesCard__heading {
  position: relative;
  z-index: 10;
  transition: all 0.17s ease-in-out;
}
.servicesCard:hover .servicesCard__bg {
  opacity: 1;
}
.servicesCard:hover .servicesCard__icon {
  fill: var(--color-white);
}
.servicesCard:hover .servicesCard__heading {
  color: var(--color-white);
}

.moreInfo {
  display: flex;
  column-gap: 40px;
  align-items: center;
  justify-content: center;
}

.sliderControls {
  display: flex;
  align-items: center;
}

.sliderControl {
  --btn-bg-color: var(--color-primary);
  --btn-bg-opacity: 0;
  --arrow-color: var(--color-grey-800);
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 48px;
  color: var(--color-grey-800);
  background-color: transparent;
  transition: color 0.18s ease-in-out, outline 0.07s ease-in-out;
}
.sliderControl::before {
  content: "";
  position: absolute;
  border-radius: 100%;
  background-color: var(--btn-bg-color);
  opacity: var(--btn-bg-opacity);
  transition: opacity 0.18s ease-in-out;
  inset: 0;
}
.sliderControl__icon {
  z-index: 5;
  width: 24px;
  height: 24px;
  fill: currentcolor;
}
.sliderControl:focus {
  outline: none;
}
.sliderControl:focus-visible {
  --btn-bg-opacity: 1;
  outline: 2px solid var(--color_grey_900);
  outline-offset: 1px;
  color: var(--color-white);
}
.sliderControl:hover {
  --btn-bg-opacity: 1;
  color: var(--color-white);
}

.sliderControl_light {
  --btn-bg-opacity: 0.5;
  --btn-bg-color: var(--color-white);
}
.sliderControl_light:hover {
  color: var(--color-grey-800);
}

.projectCard {
  --img-height: 345px;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
  height: 440px;
  max-width: 390px;
  border-radius: 4px;
  box-shadow: var(--main-shadow);
  background-color: var(--color-white);
  cursor: pointer;
}
.projectCard__logo {
  width: 100%;
  max-height: var(--img-height);
  transition: all 0.2s ease-in-out;
}
.projectCard__logoImg {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: top;
  transition: all 0.2s ease-in-out;
}
.projectCard__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 16px 20px 24px;
  transition: all 0.2s ease-in-out;
}
.projectCard__heading {
  margin-bottom: 4px;
}
.projectCard__btn {
  margin-top: 24px;
}
.projectCard:hover, .projectCard:focus-within {
  --img-height: 277px;
}

.projectSlider {
  position: relative;
}
.projectSlider__line {
  display: flex;
  column-gap: 30px;
}
.projectSlider__sliderControls {
