  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 1px;
  border-radius: 1px;
  background-color: var(--color-primary);
  transform: translateY(-50%);
}
.offerAccourdion__icon::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 1px;
  border-radius: 1px;
  background-color: var(--color-primary);
  transform: translateY(-50%) rotate(90deg);
  transition: transform 0.3s ease-in-out;
}
.offerAccourdion_open .offerAccourdion__content {
  padding-top: 13px;
  opacity: 1;
}
.offerAccourdion_open .offerAccourdion__icon::after {
  transform: rotate(0);
}

